import React, { Component } from 'react';
import fetch from 'isomorphic-unfetch';

class Books extends Component {
	constructor(props) {
			super(props);
			this.state = {books: []};
	}


	componentWillMount() {
	    this.setState({
	      books: this.props.bookData
	    })
    }

	render() {
		var bookArray = this.state.books;


		var books = [{id: 1, title: "lala"}, {id: 2, title: "ssss"}]
		console.log("Book array recieved: " + books);
		console.log("Book comp renders");

		const listItems = books.map(function(book) {
            return <div className="book" key={book.id}>
                          {book.title}
                    </div>;
          
      	});

      	return (
        	<div className="bookList">
            	{listItems}
        	</div>
      	);//end of return

		/*
			
	    return (
	        <div>
	            <p>Here comes books</p>
	            <div>{this.state.books}</div>
	        </div>
	    );*///end of return
	 } //end of render
} //end of component



export default Books;


